@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --foreground-colour: rgb(255,255,255);
  --background-colour: rgb(0,0,0);
  --secondary-colour: #25252A;
}

body {
  color: var(--foreground-colour);
  background: var(--background-colour);
  margin: 0;
  font-family: monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
  @apply text-lg;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

table th, table td {
  @apply pl-6 pb-3 pt-3 text-left;
}

table > thead > tr > td {
  padding-bottom: 12px;
}

table > tbody > tr > td {
  padding-bottom: 16px;
}

table > tbody > tr:nth-child(odd) {
  background-color: var(--secondary-colour);
}

table > tbody > tr:nth-child(odd) > td:first-child {
  @apply rounded-l-lg
}

table > tbody > tr:nth-child(odd) > td:last-child {
  @apply rounded-r-lg;
}

table > tbody > tr:nth-child(odd) > input{
  background-color: var(--secondary-colour);
}

table > tbody > tr:nth-child(odd) {
  background-color: var(--secondary-colour);
}

table > tbody > tr:nth-child(even) input {
  @apply w-full;
  background: var(--background-colour);
}

table > tbody > tr:nth-child(odd) input {
  @apply w-full;
  background: var(--secondary-colour);
}

table > tfoot > tr input {
  @apply w-full;
  background: var(--background-colour);
}


input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {
  background: var(--background-colour);
}

button {
  @apply hover:text-yellow-700 font-extrabold;
}

table > tbody > tr:nth-child(odd) select {
  background: var(--secondary-colour);
}

table > tbody > tr:nth-child(even) select{
  background-color: var(--background-colour);
}